.hover-mask {
	position: absolute;
	top: 0px;
	width: 90%;
	margin: 0 5%;
	text-align: center;
	background-color: rgba(168, 168, 168, 0.87);
	color: white;
	visibility: visible;
	font-weight: bold;
	font-size: 24px;
	-webkit-animation: fadeInAnimation ease 1s;
	        animation: fadeInAnimation ease 1s 
}

.hidden-hover-mask {
	position: absolute;
	top: 0px;
	visibility: hidden;
}

@-webkit-keyframes fadeInAnimation { 
    0% { 
		opacity: 0;
		visibility: visible;
    } 
    100% { 
		opacity: 1;
		visibility: hidden;
     } 
}

@keyframes fadeInAnimation { 
    0% { 
		opacity: 0;
		visibility: visible;
    } 
    100% { 
		opacity: 1;
		visibility: hidden;
     } 
} 

@-webkit-keyframes fadeOutAnimation { 
    0% { 
		opacity: 1;
		visibility: visible;
    } 
    100% { 
		opacity: 0;
		visibility: hidden;
     } 
} 

@keyframes fadeOutAnimation { 
    0% { 
		opacity: 1;
		visibility: visible;
    } 
    100% { 
		opacity: 0;
		visibility: hidden;
     } 
}

